$azul-escuro: rgb(33, 67, 81);
$carne: rgb(253, 181, 202);
$bege: rgb(254, 244, 239);
$branco: rgb(255, 255, 255);
$test: url(../source/provisorio/calcada.jpg);

@mixin splash() {
    height: 100vh;
    // background
}

@mixin centro() {
    height: 30vh;

}

@mixin metade() {
    height: 100vh;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;

    // TESTANDO
    .metade-img {
        background:
            no-repeat,
            center center,
            $test;
        width: 50vw;
    }
    
    .metade-txt {
        display: block;
        background-color: $bege;
        width: 50vw;
    }
}

.metade {
    @include metade;
}